$small: 375px;
$middle: 768px;
$desktop: 1024px;
$mbpro: 1280px;

@mixin responsive($media) {
	@if $media == mobile {
		@media only screen and (max-width: $middle - 1) {
			@content; } }
	@else if $media == tablet {
		@media only screen and (min-width: $middle) and (max-width: $desktop - 1) { // 768 - 1023
      @content; } }
	@else if $media == device {
		@media only screen and (max-width: $desktop - 1) { // -1023
      @content; } }
	@else if $media == tabletdesktop {
		@media only screen and (min-width: $middle) { // +768
      @content; } }
	@else if $media == largeDesktop {
		@media only screen and (min-width: $mbpro) { // +1280
      @content; } }
	@else if $media == desktop {
		@media only screen and (min-width: $desktop) { // +1024
      @content; } } }
